import React, {useMemo, useState, useEffect} from "react";

import {LayoutManager} from "ui-layout-manager";

import VariableTreev2 from "./VariableTreev2.json";
import VariableTreev3 from "./VariableTreev3.json";

import "bootstrap/dist/css/bootstrap.min.css";
import "./App.scss";

/**
 * Renders the application.
 *
 * @return {JSX.Element}
 */
export function App () {

    const [layout, setLayout] = useState(VariableTreev2);
    const [loading, setLoading] = useState(false);

    const registry = useMemo(() => ({
        EditorVSCode: () =>
            import("./sample_components/editor/EditorVSCode").then((m) => ({
                default: m.default || m.EditorVSCode,
            })),
        Stack: () =>
            import("./sample_components/stack/Stack").then((m) => ({
                default: m.default || m.Stack,
            })),
        Flow: () =>
            import("./sample_components/flow/Flow").then((m) => ({
                default: m.default || m.Flow,
            })),
        Map: () =>
            import("./sample_components/map/Map").then((m) => ({
                default: m.default || m.Map,
            })),
        Variables: () =>
            import("./sample_components/variable/Variable").then((m) => ({
                default: m.default || m.Variables,
            })),
        Header: () =>
            import("./sample_components/header/Header").then((m) => ({
                default: m.default || m.Header,
            }))
    }), []);

    useEffect(() => {
        document.removeEventListener("keydown", keydown, false);
        document.addEventListener("keydown", keydown, false);
        return () => {
            document.removeEventListener("keydown", keydown, false);
        };
    }, [layout]);

    const keydown = (e) => {
        switch (e.code) {
            case "ArrowRight":
                nextLayout();
                break;
            case "ArrowLeft":
                prevLayout();
                break;
            default:
                break;
        }
    };

    const nextLayout = () => {
        setLayout(VariableTreev3);
    };

    const prevLayout = () => {
        setLayout(VariableTreev2);
    };

    return (
        <LayoutManager registry={registry} ldf={layout}/>
    );
}
